import request from '../../utils/request.js'

export default{
	data(){
		return{
			
		}
	},
	methods:{
		getOpenId(){
			// 通过wx.login()获取登录凭证code
			wx.login({
				success:async (res)=>{
					console.log(res)
					let code=res.code;
					// 将code发送给小程序服务器
					let result=await request('/getOpenId',{code})
				}
			})
		}
	},
	mounted(){
		wx.getUserInfo({
			success:(res)=>{
				console.log(res)
			}
		})
	}
}
